@import "../../../../common/Styles/SASS/variables";
@import "../../../../common/Styles/SASS/devices";
@import "../../../../common/Styles/SASS/mixins";

.nav {
  width: 58%;
  @include flexCenter(space-between);

  .link {
    position: relative;
    @include fontParams(16px, 22px, 300, $primaryTextColor);
    margin-right: 9px;

    &::after {
      content: '';
      position: absolute;
      bottom: -5px;
      left: 0;
      right: 0;
      width: 0;
      height: 10px;
      border-bottom: 1px solid rgba(177, 177, 177, 0.3);
    }

    &:hover {
      cursor: pointer;
      color: $secondaryTextColor;
      opacity: 0.9;

      &:after {
        width: 100%;
      }
    }

    &:nth-child(5) {
      margin-right: 2px;
    }

    &:nth-child(6) {
      margin-right: 14px;
    }

  }

  @media screen and (max-width: $tablet) {
    display: none;
  }

  @media screen and (max-width: $mobileL) {

  }

}